import React, { useState, useEffect } from 'react';
import * as cards from "media";
import styles from './call.module.scss';
function Call3({ ishm3Scroll, setCallExitModal }) {
  const [checkState, setCheckState] = useState(false);
  const [item, setItem] = useState({
    name: "",
    phone: "",
    email: "",
    contents: "",
    etc: "",
  })
  useEffect(() => {
    if (item.name == "" && item.phone == "" && item.email == "" && item.contents == "" && item.etc == "") {
      setCallExitModal(false);
    } else {
      setCallExitModal(true);
    }
  }, [item])
  return (
    <>
      <div className={styles["m-call"]}>
        <div><span></span><input value={item.name} onChange={(e) => setItem({ ...item, name: e.target.value })} /></div>
        <div><span></span><input value={item.phone} onChange={(e) => setItem({ ...item, phone: e.target.value })} /></div>
        <div><span></span><input value={item.email} onChange={(e) => setItem({ ...item, email: e.target.value })} /></div>
        <div><span></span><textarea className={styles["m-call-content"]} value={item.contents} onChange={(e) => setItem({ ...item, contents: e.target.value })} /></div>
        <div>
          <span></span>
          <textarea value={item.etc} onChange={(e) => setItem({ ...item, etc: e.target.value })}/>
        </div>
        <div>
          <textarea value={privacyMessage} />
        </div>
        <div>
          <span></span>
          <div>
            <span onClick={() => setCheckState(!checkState)}
              style={{
                background: checkState ? "#0032A0" : "",
                borderColor: checkState ? "#0032A0" : "",
              }}
            >
              {"\u2713"}
            </span>
            <span>{'    .'}</span>
          </div>
        </div>
        <div><div onClick={() => { alert('.'); setItem({ name: "", phone: "", email: "", contents: "", etc:"" }) }}></div></div>
      </div>
    </>
  );
}

