import React, { useState } from "react";
import { RenderAfterNavermapsLoaded, NaverMap, Marker } from "react-naver-maps";
import "./Map.scss";

function Map() {
  const [{ lat, lng }] = useState({
    lat: 37.589038340304555,
    lng: 127.05935059022022,
  });

  const NaverMapAPI = () => {
    return (
      <NaverMap
        mapDivId={"maps-getting-started-uncontrolled"} // default: react-naver-map
        style={{
          width: "60%", //   
          height: "80%", //   
        }}
        defaultCenter={{ lat: lat, lng: lng }} //   
        defaultZoom={17} //    
      >
        <Marker
          position={{ lat: lat, lng: lng }}
          animation={2}
          onClick={() => {
            alert(" ().");
          }}
        />
      </NaverMap>
    );
  };

  return (
    <div className="map">
      <div className="map-naver">
        <RenderAfterNavermapsLoaded
          ncpClientId={"cc12woewbx"}
          error={<p>Maps Load Error</p>}
          loading={<p>Maps Loading...</p>}
        >
          <NaverMapAPI />
        </RenderAfterNavermapsLoaded>
      </div>
      <div className="map-text">
        <div className="map-grid">
          <span className="address first">02496  </span>
          <span className="address second"> 12 1, 7</span>
          <span></span>
          <span></span>
          <span className="info title"> </span>
          <span className="info content">email@gmail.com</span>
          <span className="info title"></span>
          <span className="info content">02-6490-5316</span>
          <span className="info title"></span>
          <span className="info content">050-7534-5819</span>
        </div>
      </div>
    </div>
  );
}

export default Map;
